@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100vw;
  height: 7rem;
  background: rgb(228 224 224);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.header_data {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  overflow: unset;
}
.header_data .rightSide {
  width: auto;
}
.logoLink {
  text-decoration: none;
  list-style: none;
  color: black;
}
/* toggle  */
.img img {
  display: none;
  width: 4rem;
  position: absolute;
  right: 2rem;
  top: 2rem;
  cursor: pointer;
}
.img img:hover {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.logo,
.logo div {
  display: flex;
  align-items: center;
  font-size: 2rem;
}
.logo div {
  padding-left: 1rem;
  font-size: 2.5rem;
  font-family: "Merriweather", serif;
}
.logo img {
  width: 5rem;
  border-radius: 1.3rem;
}
.menu ul {
  display: flex;
  font-size: 2rem;
  list-style: none;
}
ul li {
  margin-left: 1.6rem;
  margin-right: 0.6rem;
  /* font-size: 2.6rem; */
  /* border: 1px solid red;
  border-radius: 6px;
  padding: 0 0.4rem;
  background: rgb(222 88 88);    */
}
ul li a {
  color: black;
  text-decoration: none;
}
ul li a button {
  font-size: 1.3rem;
  color: black !important;
  font-size: 1.3rem !important;
}
.active > button {
  background: #7fabd9;
}

@media (max-width: 851px) {
  .rightSide {
    display: none;
  }
  .img img {
    display: initial;
  }
}

