.ContactPage {
  max-width: 100vw;
  margin: 8rem auto 0;
  min-height: 50vw;
  z-index: 5;
}

.contactForm {
  /* position: absolute; */
  padding: 70px 50px;
  /* padding-left: 250px; */
  /* margin-left: auto; */
  border-radius: 5px;
  width: calc(60%);
  height: 65%;
  background: rgb(255, 255, 255);
  box-shadow: 0 50px 50px rgba(0, 0, 0, 0.25);
  margin: auto;
}
.contactForm h2 {
  color: #0f3959;
  font-size: 24px;
  font-weight: 400;
  align-items: initial;
}
.contactForm .formBox {
  position: relative;
  display: flex;
  /* flex-direction: column; */
  /* justify-content: center; */
  align-items: center;
  flex-wrap: wrap;

  padding-top: 30px;
}
.contactForm .formBox .inputBox {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 30px;
}
.contactForm .formBox .inputBox.w50 {
  width: 48%;
}
.contactForm .formBox .inputBox.w100 {
  width: 100%;
}
.contactForm .formBox .inputBox.w100 span {
  align-items: flex-start;
}
.contactForm .formBox .inputBox input,
.contactForm .formBox .inputBox textarea {
  width: 100%;
  resize: none;
  padding: 5px 0;
  font-size: 19px;
  font-weight: 300;
  color: #333;
  border: none;
  outline: none;
  border-bottom: 1px solid #777;
}
.contactForm .formBox .inputBox textarea {
  height: 120px;
}
.contactForm .formBox .inputBox span {
  position: absolute;
  color: rgb(18, 219, 119);
  left: 0;
  padding: 5px 0;
  pointer-events: none;
  font-size: 18px;
  font-weight: 300;
  transition: 0.3s;
}
.contactForm .formBox .inputBox input:focus ~ span,
.contactForm .formBox .inputBox input:valid ~ span,
.contactForm .formBox .inputBox textarea:focus ~ span,
.contactForm .formBox .inputBox textarea:valid ~ span {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 1px;
  color: rgb(1, 102, 52);
}
.contactForm .formBox .inputBox input[type="submit"] {
  position: relative;
  cursor: pointer;
  background: #095a55;
  color: white;
  border: none;
  max-width: 150px;
  border-radius: 2px;
  padding: 10px;
}
.contactForm .formBox .inputBox input[type="submit"]:hover {
  background: #36f0e3;
  /* background-color: white; */
}

/* responsiveness  */

@media (max-width: 1200px) {
  .contactForm {
    position: relative;
    width: calc(100% - 550px);
    /* padding-left: 0px; */
    /* margin-left: 0; */
    padding: 40px;
    height: 550px;
    box-shadow: 0 50px 50px rgba(0, 0, 0, 0.25);
  }
}

@media (max-width: 990px) {
  .contactForm {
    width: 100%;
    height: auto;
  }
}

@media (max-width: 721px) {
  .contactform {
    padding: 25px;
  }

  .contactForm .formBox .inputBox.w50 {
    width: 100%;
  }
}

