.outer {
  display: none;
  position: absolute;
  z-index: 1;
  /* top: 9rem; */
  top: 9rem;
  right: -35rem;
  width: 28rem;
  /* border: 2px solid red; */
  border-radius: 1rem;
  padding: 3rem 2rem 5rem 2rem;
  background-color: rgb(76, 255, 219);
  /* filter: opacity(0.3); */
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  transition: all 0.5s linear;
}
.outer.active {
  display: initial;
  right: 1rem;
  z-index: 10;
}
.inner {
  /* border: 1px solid red; */
  border-radius: 1rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  transition: all 0.3s linear;
}
.inner:hover {
  background-color: rgb(134, 251, 228);
}
.inner .text {
  position: relative;
  padding: 1rem;
  font-size: 3rem;
  text-shadow: 2px 1px 1px green;
}
.sMenu {
